.input__group {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
  position: relative;
}

.label {
  font-size: 1.6rem;
  margin-bottom: 0.5rem;
  color: #b7b5b5;
}

.input {
  padding: 1.1rem 1rem;
  border-radius: 0.4rem;
  border: 0.1rem solid #acacac;
  margin: 0.7rem 0 1.4rem 0;
  font-size: 1.7rem;
  font-weight: 300;
  width: 100%;
}

.input:focus {
  outline: none;
}

.input::placeholder {
  color: #444141;
  font-weight: 400;
  font-size: 1.4rem;
  letter-spacing: 0.1rem;
}

.input[type="password"]::placeholder {
  font-size: 1.2rem;
}
.input[type="text"]::placeholder {
  font-size: 1.2rem;
}

.eye {
  position: absolute;
  top: 63%;
  right: 5%;
  transform: translateY(-50%);
}

.eyeSvg svg {
  width: 3rem;
  height: 2.5rem;
  fill: #313030;
}

@media screen and (max-width: 1068px) {
  .input[type="password"]::placeholder {
    font-size: 1.4rem;
  }
  .input[type="text"]::placeholder {
    font-size: 1.4rem;
  }
}
